import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

const App = () => {
    return (
        <Router basename='/'>
            <div>
                <nav>
                    <ul>
                        <li>
                        <Link to="/">Home</Link>
                        </li>
                        <li>
                        <Link to="/addExpense">Add Expense</Link>
                        </li>
                        <li>
                        <Link to="/removeExpense">Remove Expense</Link>
                        </li>
                        <li>
                        <Link to="/viewExpenses">View Expenses</Link>
                        </li>
                    </ul>
                </nav>
            </div>
            <Switch>
                <Route path='/addExpense' >
                    <div>Add Expense</div>
                </Route>
                <Route path='/removeExpense' >
                    <div>Remove Expense</div>
                </Route>
                <Route path='/viewExpenses' >
                    <div>View Expenses</div>
                </Route>
                <Route path='/' >
                    <div>Home</div>
                </Route>
            </Switch>   
        </Router>
    )
}

export default App;